/* General styles */

  .contact-container {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    padding: 40px;
    max-width: 1200px;
    margin: 0 auto;
    background-color: whitesmoke;
    /* border-radius: 10px; */
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  }
  
  .contact-form, .contact-info {
    width: 45%;
  }
  
  /* Contact Form Styles */
  .contact-form h2 {
    color: #2563eb;
    border-bottom: 2px solid #2563eb;
    display: inline-block;
    padding-bottom: 5px;
  }
  
  .contact-form label {
    display: block;
    margin-top: 20px;
    color: #333;
  }
  
  .contact-form input,
  .contact-form textarea {
    width: 100%;
    padding: 10px;
    margin-top: 10px;
    margin-bottom: 20px;
    border: 1px solid #ccc;
    border-radius: 5px;
    font-size: 16px;
  }
  
  .contact-form button {
    background-color: #2563eb;
    color: white;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
  }
  
  .contact-form button:hover {
    background-color: #003cbf;
  }
  
  /* Contact Info Styles */
  .contact-info h4 {
    margin-bottom: 20px;
    color: #333;
  }
  
  .contact-info p {
    color: #333;
    margin: 10px 0;
  }
  
  .contact-info i {
    color: #2563eb;
    margin-right: 10px;
  }
  
  .social-icons a {
    color: #2563eb;
    font-size: 20px;
    margin-right: 15px;
    text-decoration: none;
  }
  
  .social-icons a:hover {
    color: #003cbf;
  }
  
  /* Google Maps iframe */
  .contact-info iframe {
    margin-top: 20px;
    border: 0;
    border-radius: 10px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  }
  
  /* Responsive Design */
  
  /* Tablets and small desktops */
  @media screen and (max-width: 992px) {
    .contact-container {
      flex-direction: column;
      padding: 30px;
    }
  
    .contact-form, .contact-info {
      width: 100%;
      margin-bottom: 40px;
    }
    
    .contact-info iframe {
      width: 100%;
    }
  }
  
  /* Mobile devices (portrait and small screens) */
  @media screen and (max-width: 768px) {
    .contact-container {
      padding: 20px;
    }
  
    .contact-form button {
      width: 100%;
      padding: 12px;
      font-size: 14px;
    }
  
    .contact-form input,
    .contact-form textarea {
      font-size: 14px;
      padding: 8px;
    }
  
    .social-icons a {
      font-size: 18px;
    }
  }
  
  @media screen and (max-width: 480px) {
    .contact-form h2 {
      font-size: 24px;
    }
  
    .contact-form input,
    .contact-form textarea {
      font-size: 12px;
    }
  
    .contact-form button {
      font-size: 12px;
      padding: 10px;
    }
  
    .social-icons a {
      font-size: 16px;
      margin-right: 10px;
    }
  
    .contact-info h4 {
      font-size: 18px;
    }
  }
  